import React from 'react';
import Container from 'react-bootstrap/Container';
import Tremor from './components/Tremor';
import {BrowserRouter as Router, Switch, Route} from "react-router-dom";
import './App.css';

/**
 * 
 * ==== Rebuilding this app from scratch ====
 * npm install react-bootstrap bootstrap        # Don't forget to add the reference in your index.js
 * npm install react-router-dom
 * The last is so it can go in a sub directory on a server
 */


function App() {
  return (
    <div className="App">
      <Router basename={'/tremor'}>
        <Container>
          <Switch>                                          
            <Route path="/">
              <Tremor />
            </Route>
          </Switch>
        </Container>
      </Router>
    </div>
  );
}

export default App;
